<template>
  <div>
  <el-table
    :data="tableData"
    style="width: 100%">
    <template v-for="(item, index) in tablebar" >
      <el-table-column :key="index" align="center" :prop="item.prop" :label="item.label"></el-table-column>
    </template>
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button @click="controller(scope.$index, scope.row.tag)" type="text" size="small">{{scope.row.state === '已停止' ? '运行' : '停止'}}</el-button>
        <el-button @click="del(scope.row.tag)" type="text" size="small">删除</el-button>
      </template>
    </el-table-column>
<!--    <el-table-column-->
<!--      prop="rurl"-->
<!--      label="请求地址">-->
<!--    </el-table-column>-->
<!--    <el-table-column-->
<!--      prop="durl"-->
<!--      label="转发地址">-->
<!--    </el-table-column>-->
<!--    <el-table-column-->
<!--      prop="state"-->
<!--      label="状态">-->
<!--    </el-table-column>-->
<!--    <el-table-column-->
<!--      fixed="right"-->
<!--      label="操作"-->
<!--      width="100">-->
<!--      <template slot-scope="scope">-->
<!--        <el-button @click="start(scope.$index, scope.row.tag)" type="text" size="small">开始</el-button>-->
<!--      </template>-->
<!--    </el-table-column>-->
  </el-table>
    <el-button type="text" @click="dialogFormVisible = true">添加</el-button>
    <el-dialog title="添加信息" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="编号" :label-width="formLabelWidth">
          <el-input v-model="form.id" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="名称" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="标签" :label-width="formLabelWidth">
          <el-input v-model="form.tag" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="请求地址" :label-width="formLabelWidth">
          <el-input v-model="form.rurl" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="转发地址" :label-width="formLabelWidth">
          <el-input v-model="form.durl" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="上下游信息" :label-width="formLabelWidth">
          <el-select v-model="form.direction" placeholder="上/下游">
            <el-option label="上游" value="0"></el-option>
            <el-option label="下游" value="1"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="add">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>

import { eventsource, start, stop, del, add } from "@/server/api.js";
export default {
  data() {
    return {
      tablebar: [
        {
          label: '标签',
          prop: 'tag'
        },
        {
          label: '请求地址',
          prop: 'rurl'
        },
        {
          label: '转发地址',
          prop: 'durl'
        },
        {
          label: '状态',
          prop: 'state'
        }
      ],
      tableData: [],
      dialogFormVisible: false,
      form: {
        id: '',
        name: '',
        tag: '',
        rurl: '',
        durl: '',
        time: 2,
        direction:''

      },
      formLabelWidth: '120px'
    }
  },
  mounted() {

    // this.$http.get('/api/cycletask/list').then(data => {
    //   console.log(data)
    // })

    eventsource((event) => {
      let data = JSON.parse(event.data)
      if (data !== null) {
        data.forEach(data => {
          data.state = data.state === true ? "正在运行" : "已停止"
        })
        this.tableData = data
      }
    })



  },
  methods: {
    controller(index, tag) {
      this.tableData[index].state === '已停止' ? this.start(tag, index) : this.stop(tag, index);

    },

    start(tag, index) {
      console.log(this.tableData)
      start(tag).then(data => {
        console.log(data.Code)
        Number(data.Code) !== 0 ? alert("服务器异常") : this.tableData[index].state = '正在运行';
      })
    },

    stop(tag, index) {
      stop(tag).then(data => {
        Number(data.Code) !== 0 ? alert("服务器异常") : this.tableData[index].state = '已停止'
      })
    },

    del(tag) {
      this.tableData = this.tableData.filter(data => data.tag !== tag)
      del(tag).then(data => {
        Number(data.Code) !== 0 ? alert("服务器异常") : ''
      })
    },

    add() {
      console.log(this.form)
      add(this.form).then(data => {
        Number(data.Code) !== 0 ? (alert("服务器异常"), this.dialogFormVisible = false) : this.dialogFormVisible = false;
      })
    }

  }
}
</script>



