import conf from '@/server/index';
const axios = require('axios');


/**
 * 拼接请求地址
 * splitUrl :: (String -> String) -> String
 * @param {String} host 服务主机地址
 * @param {String} path API地址
 */


const splitUrl = (host, path) => conf[host] + path;

export default {
  post: (host, url, data, error, option) =>
    axios.post(splitUrl(host, url), data, option)
      .then(response => response.data)
      .catch((err) => {
        if (error) {
          error(err);
        } else {
          console.error(err);
        }
      }),
  get: (host, url, error, option) => {
    return axios.get(splitUrl(host, url), option)
      .then(response => response.data)
      .catch((err) => {
        if (error) {
          error(err);
        } else {
          console.log(err);
        }
      })
  },
  eventsource: (host, url, eventmessage) => {
    const source = new EventSource(splitUrl(host, url))
        source.onmessage = (data) => {
          eventmessage(data)
      }
  }
};



