import http from './request'


export const success = 0

export const getdata = () => http.get('all', '/api/test')

export const start = (data) => http.get('all', `/api/start?tag=${data}`)

export const stop = (data) => http.get('all', `/api/stop?tag=${data}`)

export const del = (data) => http.get('all', `/api/del?tag=${data}`)

export const add = (data) => http.get('all', `/api/add?id=${data.id}&name=${data.name}&tag=${data.tag}&rurl=${data.rurl}&durl=${data.durl}&time=${data.time}&direction=${data.direction}`)

export const eventsource = (data) => http.eventsource("all", "/cycletask/list", data)




